import api from "@/common/index.js";
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uCharts from '@/components/u-charts/u-charts.js';
import over from '@/components/over.vue';
import imglazy from '@/components/img-lazy/img-lazy.vue';
var _self;
var canvaPie = null;
var mvc = {
	repList: [],
	number: '',
	statusCode: '',
	p_num: '',
	isShow: false,
	list: '',
	hide: false,
	name: '',
	current: -1,
	over: false,
	cWidth: '',
	cHeight: '',
	pixelRatio: 1,
	qiunHdid: false,
	textarea: "",
	info: {
		name: '',
		num: '',
		time: ''
	},
	numList: [],
}
export default {
	components: {
		uniNavBar,
		imglazy,
		over
	},
	data() {
		return {
			mvc: {
				repList: [],
				number: '',
				statusCode: '',
				p_num: '',
				isShow: false,
				list: '',
				hide: false,
				name: '',
				current: -1,
				over: false,
				cWidth: '',
				cHeight: '',
				pixelRatio: 1,
				qiunHdid: false,
				textarea: "",
				info: {
					name: '',
					num: '',
					time: ''
				},
				numList: [],
			},
			imglazyStyle: 'border-radius: 10upx;'
		};
	},
	onLoad() {
		let that = this;
		setTimeout(function() {
			mvc = that.mvc;
			api.pageTitle(that.$t('repertory').title)
			_self = that;











			mvc.cWidth = uni.upx2px(750);
			mvc.cHeight = uni.upx2px(500);
			const params = uni.getStorageSync('userinfo');
			mvc.number = params.hdid;
			that.mvc = mvc;
		}, 300)
	},

	computed: {
		i18n() {
			return this.$t('repertory');
		}
	},
	methods: {
		// 选择类型
		radioChange(evt) {
			let that = this;
			for (let i = 0; i < that.$t("repertory").items.length; i++) {
				if (that.$t("repertory").items[i].value === evt.target.value) {
					if (evt.target.value == "a") {

						mvc.name = '';
						mvc.repList = [];
						mvc.qiunHdid = false;
						mvc.over = false;
						mvc.hide = false;
					};
					if (evt.target.value == "b") {

						mvc.p_num = '';
						mvc.repList = [];
						mvc.qiunHdid = false;
						mvc.over = false;
						mvc.hide = false;
					};
					mvc.current = i;

					break;
				}
			}
			that.mvc = mvc;
		},
		back() {
			// 返回上一层页面
			uni.navigateBack({
				delta: 1
			});
		},
		// 跳转详情
		navToDetailPage(item) {
			console.log(item)
			uni.navigateTo({
				url: `/pages/product/product?productId=${item}`
			})
		},
		// 调用二维码扫描
		onScan() {

			let that = this;
			uni.scanCode({
				success: function(res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
					mvc.p_num = res.result;
					that.loadPost();
				}
			});
		},
		// input
		onKeyInput(e) {
			const key = e.currentTarget.dataset.key;

			mvc[key] = e.detail.value;

		},
		search() {
			let that = this;
			if (mvc.current == -1) {
				that.$api.msg(that.$t('repertory').p1)
				return false;
			};
			if (mvc.current == 1) {
				if (mvc.p_num == "") {
					that.$api.msg(that.$t('repertory').p2)
					return false;
				}
			};
			if (mvc.current == 2) {
				if (mvc.name == "") {
					that.$api.msg(that.$t('repertory').p3)
					return false;
				}
			};
			that.loadPost()
		},

		loadPost() {
			let that = this;
			mvc.hide = false;
			mvc.qiunHdid = false;
			mvc.over = false;
			api.load(this.$t('index').loadmsg)
			let params = {
				name: mvc.name
			}
			let data = {
				p_num: mvc.p_num
			}
			mvc.repList = []
			if (mvc.current != 0) {
				that.$ajax.reqView(data, params).then(res => {
					if (res.length != 0) {
						let list = res;
						list.forEach(function(item, index) {
							item.img = that.$store.getters.imgShow + '/thumb/' + item.CODE2 + '.jpg?x-oss-process=style/300';
							if (item.QTY > 0) {
								item.all = parseFloat(parseInt(item.RTLPRC) * parseFloat(item.QTY))
							} else {
								item.all = 0
							}
						})
						mvc.statusCode = res.statusCode;
						mvc.repList = list;
						console.log(mvc.repList);
						mvc.over = true;
						mvc.p_num = "";
					} else {
						mvc.hide = true
					}

					uni.hideLoading();
				})
			} else {
				this.repCharts();
			}
			that.mvc = mvc;
		},

		repCharts() {
			let that = this;
			that.$ajax.reqRank().then(res => {
				let LineA = {
					series: []
				};

				mvc.info = res.zong;


				for (let i in res.money) {
					let title = "";
					let arry = "";

					switch (i) {
						case '01':
							title = "家居百货";
							arry = {
								name: that.$t('repertory').category[0],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry);

							break;
						case '02':
							title = "健康美容";
							arry = {

								name: that.$t('repertory').category[1],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '03':
							title = "饰品系列";
							arry = {

								name: that.$t('repertory').category[2],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '04':
							title = "文体礼品";
							arry = {

								name: that.$t('repertory').category[3],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '05':
							title = "季节产品";
							arry = {

								name: that.$t('repertory').category[4],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '06':
							title = "精品包饰";
							arry = {

								name: that.$t('repertory').category[5],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '07':
							title = "儿童玩具";
							arry = {

								name: that.$t('repertory').category[6],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '08':
							title = "毛绒公仔";
							arry = {

								name: that.$t('repertory').category[7],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '09':
							title = "数码配件";
							arry = {

								name: that.$t('repertory').category[8],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '10':
							title = "休闲食品";
							arry = {

								name: that.$t('repertory').category[9],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)
							break;
						case '14':
							title = "物料耗材";

							arry = {

								name: that.$t('repertory').category[10],
								data: res.money[i],
								qty: res.num[i],
								show: true
							}
							LineA.series.push(arry)

					}
				}

				that.showPie("canvasPie", LineA);
				mvc.qiunHdid = true;
				mvc.numList = LineA.series;
				that.mvc = mvc;
				uni.hideLoading();
			})
		},
		showPie(canvasId, chartData) {
			canvaPie = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'pie',
				fontSize: 11,
				padding: [15, 15, 0, 15],
				legend: {
					show: true,
					padding: 5,
					lineHeight: 11,
					margin: 0,
				},
				background: '#FFFFFF',
				pixelRatio: mvc.pixelRatio,
				series: chartData.series,
				animation: true,
				width: mvc.cWidth * mvc.pixelRatio,
				height: mvc.cHeight * mvc.pixelRatio,
				dataLabel: true,
				extra: {
					pie: {}
				},
			});
			uni.hideLoading();
		},
		touchPie(e) {
			canvaPie.showToolTip(e, {
				format: function(item) {
					return item.name + ':' + item.data + "元"
				}
			});

		},
		changeData() {
			if (isJSON(mvc.textarea)) {
				let newdata = JSON.parse(mvc.textarea);
				canvaPie.updateData({
					series: newdata.series,
					categories: newdata.categories
				});
			}
		}
	}
}
